.SelectGroupLabel{border-bottom:1px solid var(--color-bg-border);color:var(--color-typo-secondary);font-size:var(--font-size);letter-spacing:.07em;padding:var(--vertical-space) var(--horizontal-space);text-transform:uppercase}.SelectGroupLabel_size_xs{--horizontal-space:calc(var(--control-space-xs)*0.5);--vertical-space:calc(var(--control-space-s)*0.25);--font-size:calc(var(--control-text-size-xs) - 2px)}.SelectGroupLabel_size_s{--horizontal-space:calc(var(--control-space-s)*0.5);--vertical-space:calc(var(--control-space-s)*0.25);--font-size:calc(var(--control-text-size-s) - 4px)}.SelectGroupLabel_size_m{--horizontal-space:calc(var(--control-space-m)*0.5);--vertical-space:calc(var(--control-space-s)*0.5);--font-size:calc(var(--control-text-size-m) - 4px)}.SelectGroupLabel_size_l{--horizontal-space:calc(var(--control-space-l)*0.5);--vertical-space:calc(var(--control-space-s)*0.5);--font-size:calc(var(--control-text-size-l) - 4px)}.SelectGroupLabel_indent_increased{padding-left:calc(var(--horizontal-space)*2.6)}